import React,{useState} from 'react'
import {connect} from 'react-redux'
import './login.css'
import {datalist} from './actionCreators'
import './com.css'

const Com=(props)=>{
    const [isShow,setShow]=useState(false);
    const lis=()=>{
        props.dispatch(datalist())
    }
    return <div className="com" onLoad={lis()}>
        <ul>
        {
            props.datalist.map((item,idx)=><li onClick={()=>props.data.history.push(`/com/cargo1/:${item.id}`)}>
                    <img src={item.img}/>
                    <p>{item.title}</p>
                    <div>
                        <span>300人已买</span>
                        <p>￥{item.price}</p>
                    </div>
            </li>
            )
        }
        </ul>
    </div>
}

const mapStateToProps=(state)=>{
    return{
        datalist:state.datalist
    }
}

export default connect(mapStateToProps)(Com)

