import React from 'react'
import './Home.css'
import Lunbotu from './Lunbotu';
import Com from './Com';
import Mytab from './Mytab';
// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>
const Home=(props)=>{
  return(
    <div id='home1'>
      <div id='search1' onClick={()=>props.history.push('/search')}>
        <h2>淘</h2>
        <div id='searchto'>
        <p>寻找宝贝店铺</p>
        </div>
      </div>

      <div id='lunbotu'>
        <Lunbotu/>
      </div>

      <div id='com'>
        <Com data={props}/>
      </div>
      <Mytab/>
    </div>
  )
}
export default Home;