import React from 'react'
import {connect} from 'react-redux';
// import { datalist } from './actionCreators';
import './shoucang.css'
const Shoucang = (props) => {
    let src=[];
    for(let j=0;j<props.shoucang.length;j++){
        for(let i=0;i<props.datalist.length;i++){
            if(props.shoucang[j]==props.datalist[i].id){
                src[j]=props.datalist[i].img;
            }
        }
    }
    return (
        <div>
            <div id='back' onClick={()=>props.history.push('/home')}><i className='iconfont icon-buoumaotubiao09'
            ></i></div>
            <div id='mystar'><p>我的收藏</p></div>
            <div>
                <ul>
                    {
                        src.map((item,idx)=><li>
                            <div className='shoucangjia'>
                                <img src={item} className="image"/>
                                <div className='del' onClick={()=>{props.dispatch({
                                    type:'DEL_TODO',
                                    index:idx
                                })}}><p>删除</p></div>
                            </div>
                        </li>
                        )
                    }
                </ul>
            </div>
        </div>
    )
}

const mapStateToProps=(state)=>{
    return {
        shoucang:state.shoucang,
        datalist:state.datalist

    }
}
export default connect(mapStateToProps)(Shoucang)
