import './cargo1.css';
import React,{useState} from 'react'
import {connect} from 'react-redux'
import {userinfo} from './actionCreators'
const Cargo1 = (props) => {
    const [isShow,setShow]=useState(false);
    const log=()=>{
        props.dispatch(userinfo())
    }
    let src='';
    let i=0;
    let idx=props.match.url.split(':')[1];
    let length=props.datalist.length;
    for(let i=0;i<length;i++){
        if(props.datalist[i].id===Number(idx)){
            src=props.datalist[i].img;
        }
    }

    return (
        <div id='cargo1'>
            <div>
                <div className='image'><img src={src}/></div>
                <div id='back' onClick={()=>props.history.goBack()}><i className='iconfont icon-buoumaotubiao09'
                ></i></div>
            </div>
            {/* bottom */}
           <div id='bot'>
               <ul>
                    <li>
                        <div class='cargoi'>
                            <i className='iconfont icon-dianpu1'></i>
                            <p>店铺</p>
                        </div>
                    </li>
                    <li>
                        <div class='cargoi'>
                            <i className='iconfont icon-kefu'></i>
                            <p>客服</p>
                        </div>
                    </li>
                    <li>
                        <div class='cargoi'>
                            {
                                props.shoucang.filter(item=>item==idx).length==0?
                                <i id="start" className='iconfont icon-shoucang' 
                                onClick={()=>{
                                    if(props.userinfo!=''){
                                        var starti=document.getElementById('start');
                                    starti.className='iconfont icon-ai-mark'
                                    props.shoucang.push(idx);
                                    // console.log(props.userinfo=='')
                                    }else{
                                        props.history.push('/login');
                                    }
                                    
                                }}
                                ></i>:
                                <i id="start" className='iconfont icon-ai-mark'
                                ></i>
                            }
                            <p>收藏</p>
                        </div>
                    </li>
               </ul>
                <div class='cargodiv1'>
                    <p>加入购物车</p>
                </div>
                <div class='cargodiv2'>
                    <p>立即购买</p>
                </div>
           </div>
        </div>
    )
}

const mapStateToProps=(state)=>{
    return{
        datalist:state.datalist,
        shoucang:state.shoucang,
        userinfo:state.userinfo
    }
    
}
export default connect(mapStateToProps)(Cargo1)
