import React,{useState} from 'react'
import {connect} from 'react-redux'
import {ActivityIndicator} from 'antd-mobile'
import './login.css'
import {userinfo} from './actionCreators'
const Login = (props) => {
    const [isShow,setShow]=useState(false);
    const log=()=>{
        props.dispatch(userinfo())
    }
    return (
        <div>
            <div class="tao"><p>淘</p></div>
            <div class="inp">
                <input class="user" name="username" placeholder="手机号/邮箱/会员名" type="text"/>
                <input class="pwd" name="password" placeholder="请输入登录密码" type="password"/>
                <div class="code">
                    <p class="code1">短信验证码登录</p>
                    <p class="code2">免费注册</p>
                </div>
                
            </div>
            <div>
                {   
                    props.userinfo?
                    <div>
                        {props.userinfo} 登陆成功 正在跳转...
                        {
                            props.history.goBack()
                        }
                    </div>:
                    <div class="btn" onClick={log()}>
                        <p>登录</p>
                    </div>
                }
                <ActivityIndicator 
                toast
                text="登录中..."
                animating={isShow}
            />
            </div>
        </div>
    )
}
const mapStateToProps=(state)=>{
    return{
        userinfo:state.userinfo
    }
}

export default connect(mapStateToProps)(Login)
