import { Carousel, WingBlank } from 'antd-mobile';
import React from 'react';
class Lunbotu extends React.Component {
  state = {
    data: ['1', '2', '3','4'],
    imgHeight: 176,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['https://picasso.alicdn.com/imgextra/i2/382208/O1CNA11UbqJH100ff4a26c580b0b7653000k_!!382208-0-picassobanner', 
        'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50',
        'https://picasso.alicdn.com/imgextra/i2/382208/O1CNA11UbqJH100ff4a26c580b0b7653000k_!!382208-0-picassobanner',
        'https://img.alicdn.com/imgextra/i2/96/O1CN014g0ADB1Ca0eWkU60Z_!!96-0-luban'],
        
      });
    }, 100);
  }
  render() {
    return (
      <WingBlank>
        <Carousel className="space-carousel"
          frameOverflow="visible"
          cellSpacing={0}
          slideWidth={1}
          autoplay
          infinite
          
          afterChange={index => this.setState({ slideIndex: index })}
        >
          {this.state.data.map((val, index) => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{
                display: 'block',
                position: 'relative',
                top: this.state.slideIndex === 0,
                height: this.state.imgHeight,
              }}
            >
              <img
                style={{width:'100%',height:'20px'}}
              //  src={require(`./image/${val}.jpg`)}
              src={`${val}.jpg`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}

export default Lunbotu;