import React from 'react'
import {BrowserRouter as Router,Redirect,Route,Switch} from 'react-router-dom'
import Home from './Home';
import Buycar from './Buycar';
import List from './List';
import Mine from './Mine';
import Login from './Login';
import {Provider} from 'react-redux';
import store from './store';
import Search from './Search';
import Cargo1 from './Cargo1';
import Com from './Com'
import Shoucang from './Shoucang';
const App = ()=>{
	return <Provider store={store}> 
		<Router>
			<Redirect to='/home'></Redirect>
			<Switch>
				<Route exact path='/home' component={Home}/>
				<Route path='/gouwuche' component={Buycar}/>
				<Route path='/dingdanliebiao' component={List}/>
				<Route path='/wodetaobao' component={Mine}/>
				{/* <Route path='/gengduo' component={More}/> */}
				<Route path='/search' component={Search}/>
				<Route path='/com/cargo1/:id' component={Cargo1}/>
				<Route path='/com' component={Com}/>
				<Route path='/login' component={Login}/>
				<Route path='/shoucang' component={Shoucang}/>
			</Switch>	
		</Router>
	</Provider>
	
}
export default App;



















