import React from 'react';
import 'antd-mobile/dist/antd-mobile.css'; 
import{TabBar} from 'antd-mobile';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';

class Mytab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    //   selectedTab: 'blueTab',
      hidden:false,
      fullScreen: false,
    };
  }

  render() {
    const {pathname}=this.props.location;
    return (        
      <div style={{   position:'fixed',bottom:'0',width: '100%'}}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33A3F4"
          //可以改下面图标的背景色
		      barTintColor="white"
        >
          <TabBar.Item
            title="首页"
            key="首页"
            icon={
                <i className='iconfont icon-Homehomepagemenu'></i>
            }
            selectedIcon={
                <i className='iconfont icon-Homehomepagemenu1' ></i>
            }
			      selected={pathname === '/home'}
			//显示信息1条
            // badge={1}
            onPress={() => {
              this.props.history.push('/home');
            }}
            data-seed="logId"
          >
          </TabBar.Item>

          <TabBar.Item
            icon={
				<i className='iconfont icon-shoucang'></i>
			}
			selectedIcon={
				<i className='iconfont icon-shoucang'></i>
			}
            title="收藏"
            key="收藏"
            // badge={'new'}显示信息new红标
            selected={pathname === '/login'}
            onPress={() => {
              if(this.props.userinfo!=''){
                  this.props.history.push('/shoucang')
              }else{
                  this.props.history.push('/login')
              }
              
            }}
            data-seed="logId1"
          >
          </TabBar.Item>

          <TabBar.Item
            icon={
                <i className='iconfont icon-dingdan'></i>
            }
            selectedIcon={
                <i className='iconfont icon-dingdan1'></i>
            }
            title="订单列表"
            key="订单列表"
            // dot
            selected={pathname === 'dingdanliebiao'}
            onPress={() => {
                this.props.history.push('/dingdanliebiao')
            }}
          >
          </TabBar.Item>

		  <TabBar.Item
            icon={
                <i className='iconfont icon-wode'></i>
            }
            selectedIcon={
                <i className='iconfont icon-wode2'></i>
            }
            title="我的淘宝"
            key="我的淘宝"
            selected={pathname === '/wodetaobao'}
            onPress={() => {
                this.props.history.push('/wodetaobao')
            }}
          >
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}
const mapStateToProps=(state)=>{
  return {
      shoucang:state.shoucang,
      datalist:state.datalist,
      userinfo:state.userinfo
  }
}
export default connect(mapStateToProps)(withRouter(Mytab))
