const datalist = (datalist)=>{
    return (dispatch)=>{
        fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist', {
            method: 'post',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then(res => res.json())
            .then(res => {
                dispatch({
                    type: 'LIST',
                    datalist:res.data
                })
            })
    }
}

const userinfo = (userinfo)=>{
    return (dispatch)=>{
        fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login', {
            method: 'post',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then(res => res.json())
            .then(res => {
                dispatch({
                    type: 'LOGIN',
                    userinfo: res.data.name
                })
            })
    }
}
export {datalist,userinfo}
